import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { assestsAcademicDepartmentUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";

const InstituteDepartmentTheoryClassCard = ({ subject, openAs }) => {
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={
        openAs === "DEPARTMENT_HEAD"
          ? `/q/${params.username}/member/staff/academic/department/${params.name}?type=practical-detail`
          : `/q/${params.username}/institute/member/academic/department/detail?type=cls-detail&n=practical-detail`
      }
      state={{
        ...getQuery.state,
        subId: subject?.batch?._id,
        subjectName: subject?.batch?.batchName,
      }}
    >
      <div className={style.iadcc_card_hover}>
        <div className={style.sf_card_contatiner}>
          <img
            src={`${assestsAcademicDepartmentUrl}/cls-icon.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              width: "2rem",
              height: "2rem",
              borderRadius: "0",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {subject?.batch?.batchName ?? ""}
            </h6>
            <p>
              {`${subject?.batch?.batch_head?.staffFirstName ?? ""} ${
                subject?.batch?.batch_head?.staffMiddleName ?? ""
              } ${subject?.batch?.batch_head?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default InstituteDepartmentTheoryClassCard;
